import { 
  getArticles, 
  getYourFeedArticles, 
  getTags,
  addFavorite,
  deleteFavorite
} from '@/api/articles.js'
import { mapState } from 'vuex'
import ArticleList from '@/components/article-list'
export default {
    async asyncData ({ query }) {
        const limit = 20;
        const { page = 1, tag, tab='global_feed' } = query
        const loadArticles = tab === 'your_feed' ?  getYourFeedArticles : getArticles
        let articles = []
        let articlesCount = 0
        let tags = [];
        try{
          let [tagsRes, articleRes] = await Promise.all([
            getTags(),
            loadArticles({
              limit,
              offset: (page - 1) * limit,
              tag: query.tag
            })
          ])
          tags = tagsRes.tags
          articlesCount = articleRes.articlesCount
          articles = articleRes.articles
          articles && articles.forEach(item => {
            item.disabled_favorite = false
          })
        }catch(err) {
          console.log(err)
        }
        return {
            articles,
            articlesCount,
            tags,
            page,
            limit,
            tag,
            tab
        }
    },
    
    components: { ArticleList },
    watchQuery: ['page', 'tag', 'tab'],
    computed: {
        ...mapState(['user']),
        totalPage () {
            return Math.ceil(this.articlesCount / this.limit)
        }
    },
    methods: {
      async onFavorite(article) {
        try{
          article.disabled_favorite = true
          if(article.favorited) {
            await deleteFavorite(article.slug)
            article.favoritesCount += -1
            article.favorited = false
          }else{
            await addFavorite(article.slug)
            article.favoritesCount += 1
            article.favorited = true
          }
          article.disabled_favorite = false
        }catch(err) {}
      }
    }
}
